.message{
        width: 80%;
        height: 85vh;
        float: right;
        position: relative;
        overflow-y: scroll;
}
.message-input{
    width: 70vw;
    position: relative;
    left: 3vw;
}
.message-content{
    margin-bottom: 2vh;
    position: relative;
    left: 3vw;
    color:rgb(0, 0, 0,0.9);
    font-size: 1.2vw;
    height: 10vh;
    width: 70vw;
    background-color: rgb(139, 124, 226);
}
.message-love{
    position: absolute;
    bottom: 0;
    right: 4px;
}